<template>
    <div>
        <a v-show="!editMode" v-text="valueModel" @click.prevent="editMode=true" :style="style" href="#"></a>
        <div v-show="editMode" class="form-group">
            <div style="position:relative">
                <input type="text" class="form-control" ref="datepicker" autocomplete="off"/>
            </div>
            <div>
                <button type="button" class="btn btn-primary btn-sm" @click="onUpdate">
                    <i class="fa fa-fw fa-check"></i>
                </button>
                <button type="button" class="btn btn-default btn-sm" @click="editMode=false">
                    <i class="fa fa-fw fa-times"></i>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "x-date",
  props: {
    config: {
      type: Object,
      default: () => {
        return {
          format: "YYYY-MM-DD"
        };
      }
    },
    value: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "text"
    }
  },
  mounted() {
    $(this.$refs.datepicker).datetimepicker(this.config);
    $(this.$refs.datepicker).on("dp.change", e => {
      this.localValue = e.date.format(this.config.format);
      this.$emit("input", this.localValue);
    });
  },
  computed: {
    style() {
      let s = {};
      s["white-space"] = "pre-wrap";

      if (!this.localValue) {
        s.color = "red";
        s["font-style"] = "italic";
      }
      return s;
    },
    valueModel: {
      get() {
        if (this.editMode) {
          return this.localValue;
        }
        if (!this.localValue) {
          return "[empty]";
        }
        return this.localValue;
      },
      set(value) {
        this.localValue = value;
      }
    }
  },
  data() {
    return {
      localValue: this.value,
      editMode: false
    };
  },
  methods: {
    onUpdate() {
      this.editMode = false;
      this.$emit("input", this.localValue);
    }
  }
};
</script>
