<template >
    <div>
        <a v-if="!editMode" v-text="valueModel" @click.prevent="editMode=true; tempValue=localValue;" :style="style" href="#"></a>
        <div v-if="editMode">
            <div>
                <textarea class="form-control" v-model="tempValue" rows="10"></textarea>
            </div>
            <div>
                <button type="button" class="btn btn-primary btn-sm" @click="onUpdate">
                    <i class="fa fa-fw fa-check"></i>
                </button>
                <button type="button" class="btn btn-default btn-sm" @click="editMode=false">
                    <i class="fa fa-fw fa-times"></i>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "x-textarea",
  props: {
    value: {
      type: String,
      default: ""
    },
    emptyText: {
      type: String,
      default: "[empty]"
    }
  },
  watch: {
    value: {
      immediate: true,
      handler(val, oldVal) {
        this.localValue = val;
      }
    }
  },
  computed: {
    style() {
      let s = {};
      s["white-space"] = "pre-wrap";

      if (!this.localValue) {
        s.color = "red";
        s["font-style"] = "italic";
      }
      return s;
    },
    valueModel: {
      get() {
        if (this.editMode) {
          return this.tempValue;
        }
        if (!this.localValue) {
          return this.emptyText;
        }
        return this.localValue;
      },
      set(value) {
        this.localValue = value;
      }
    }
  },
  data() {
    return {
      localValue: this.value,
      tempValue: this.value,
      editMode: false
    };
  },
  methods: {
    onUpdate() {
      this.editMode = false;
      this.localValue = this.tempValue;
      this.$emit("input", this.localValue);
    }
  }
};
</script>
